
.food-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  width: 100%;
  height: 100%;
  background: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  transition: all 0.3s linear;
}
.food-wrapper.slide-enter,
.food-wrapper.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food-wrapper.slide-leave,
.food-wrapper.slide-enter-to {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food-wrapper .food {
  padding-bottom: 48px;
}
.food-wrapper .food .food-content {
  position: relative;
}
.food-wrapper .food .food-content .food-img {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food-wrapper .food .food-content .food-img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-wrapper .food .food-content .food-img .back {
  position: absolute;
  left: 10px;
  top: 10px;
}
.food-wrapper .food .food-content .food-img .back .icon-arrow_lift {
  display: block;
  font-size: 18px;
  font-weight: 500;
  padding: 10px;
  color: rgba(0,160,220,0.6);
}
.food-wrapper .food .food-content .content {
  padding: 18px;
  position: relative;
}
.food-wrapper .food .food-content .content .title {
  font-size: 14px;
  line-height: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-wrapper .food .food-content .content .sell {
  margin-top: 8px;
  font-size: 10px;
  line-height: 10px;
  color: #93999f;
}
.food-wrapper .food .food-content .content .sell :first-child {
  margin-right: 12px;
}
.food-wrapper .food .food-content .content .price-wrapper {
  position: relative;
  margin: 18px 0;
  height: 24px;
  line-height: 24px;
  font-weight: 700;
  font-size: 0;
  overflow: hidden;
}
.food-wrapper .food .food-content .content .price-wrapper .now {
  display: inline-block;
  font-size: 14px;
  color: #f01414;
}
.food-wrapper .food .food-content .content .price-wrapper .old {
  display: inline-block;
  font-size: 10px;
  margin-left: 8px;
  text-decoration: line-through;
  color: #93999f;
}
.food-wrapper .food .food-content .shopbtn-wrapper {
  position: absolute;
  top: 62px;
  right: 12px;
}
.food-wrapper .food .food-content .buy {
  position: absolute;
  z-index: 20;
  top: 66px;
  right: 18px;
  width: 74px;
  height: 24px;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  border-radius: 24px;
  background: #00a0dc;
  transition: all 0.2s;
}
.food-wrapper .food .food-content .buy.fade-enter,
.food-wrapper .food .food-content .buy.fade-leave-to {
  opactiy: 0;
}
.food-wrapper .food .food-content .buy.fade-leave,
.food-wrapper .food .food-content .buy.fade-enter-to {
  opactiy: 1;
}
.food-wrapper .food .info {
  padding: 18px;
}
.food-wrapper .food .info .title {
  font-size: 14px;
  line-height: 14px;
  font-weight: 500;
}
.food-wrapper .food .info .text {
  padding: 6px 8px;
  font-size: 12px;
  line-height: 24px;
  color: #4d555d;
}
.food-wrapper .food .ratinglist-wrapper .title {
  margin: 18px 0 0 18px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 500;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item {
  margin: 0 18px;
  padding: 16px 0;
  position: relative;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: "";
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .user-details {
  width: 100%;
  height: 12px;
  position: relative;
  color: #93999f;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .user-details .time {
  font-size: 10px;
  line-height: 12px;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .user-details .user {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .user-details .user .username {
  display: inline-block;
  height: 12px;
  font-size: 10px;
  line-height: 12px;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .user-details .user .avatar {
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: top;
  margin-left: 6px;
  border-radius: 50%;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .rating-content {
  margin-top: 6px;
  font-size: 0;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .rating-content .type {
  display: inline-block;
  font-size: 12px;
  line-height: 24px;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .rating-content .type.icon-thumb_down {
  color: #93999f;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .rating-content .type.icon-thumb_up {
  color: #00a0dc;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .rating-item .rating-content .text {
  display: inline-block;
  margin-left: 4px;
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.food-wrapper .food .ratinglist-wrapper .rating-wrapper .no-rating {
  padding: 16px 18px;
  font-size: 12px;
  color: #93999f;
}


