
.shopcart {
  width: 100%;
  height: 48px;
  position: fixed;
  z-index: 50;
  left: 0;
  bottom: 0;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  z-index: 49;
  font-size: 0;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #141d27;
}
.shopcart .content .content-left .cart-wrapper {
  display: inline-block;
  vertical-align: top;
  width: 56px;
  height: 56px;
  position: relative;
  top: -10px;
  background: #151f28;
  margin: 0 12px;
  padding: 6px;
  box-sizing: border-box;
  border-radius: 50%;
}
.shopcart .content .content-left .cart-wrapper .cart {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  text-align: center;
  background: #2b343c;
}
.shopcart .content .content-left .cart-wrapper .cart .icon-shopping_cart {
  font-size: 24px;
  line-height: 44px;
  color: #80858a;
}
.shopcart .content .content-left .cart-wrapper .cart .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcart .content .content-left .cart-wrapper .cart.highlight {
  background: #00a0dc;
}
.shopcart .content .content-left .cart-wrapper .num {
  width: 24px;
  height: 16px;
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  font-size: 9px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
  border-radius: 12px;
  box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.4);
  background: #f01414;
}
.shopcart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  padding-right: 12px;
  margin: 12px 0;
  font-size: 16px;
  line-height: 24px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-weight: 700;
}
.shopcart .content .content-left .price.highlight {
  color: #fff;
}
.shopcart .content .content-left .delivery-cost {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  font-weight: normal;
  line-height: 48px;
  margin: 0 12px;
}
.shopcart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
  background: #2b333b;
}
.shopcart .content .content-right.highlight {
  background: #00b43c;
  color: #fff;
}
.shopcart .content .content-right .delivery {
  padding: 0 8px;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  line-height: 48px;
}
.shopcart .ball-wrapper .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .ball-wrapper .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  transition: all 0.4s linear;
}
.shopcart .showcart-details {
  width: 100%;
  position: absolute;
  z-index: 48;
  left: 0;
  top: 0px;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  transition: all 0.5s;
}
.shopcart .showcart-details.slide-enter,
.shopcart .showcart-details.slide-leave-to {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcart .showcart-details.slide-leave,
.shopcart .showcart-details.slide-enter-to {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shopcart .showcart-details .details-header {
  height: 40px;
  padding: 0 18px;
  line-height: 40px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  background: #f3f5f7;
}
.shopcart .showcart-details .details-header .title {
  float: left;
  font-size: 14px;
  font-weight: 200;
  color: #07111b;
}
.shopcart .showcart-details .details-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .showcart-details .details-content {
  max-height: 217.5px;
  padding: 0 16px;
  overflow: hidden;
  background: #fff;
}
.shopcart .showcart-details .details-content .details-content-item {
  position: relative;
  height: 48px;
  line-height: 24px;
  padding: 12px 0;
  position: relative;
  box-sizing: border-box;
}
.shopcart .showcart-details .details-content .details-content-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: "";
}
.shopcart .showcart-details .details-content .details-content-item .name {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.shopcart .showcart-details .details-content .details-content-item .price {
  position: absolute;
  right: 95px;
  bottom: 12px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcart .showcart-details .details-content .details-content-item .shopbtn-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
  line-height: 1;
  font-size: 24px;
}
.shopcart .details-bg {
  position: fixed;
  z-index: 40;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.6);
  transition: all 0.5s;
}
.shopcart .details-bg.fade-enter,
.shopcart .details-bg.fade-leave-to {
  -webkit-backdrop-filter: blur(0);
  backdrop-filter: blur(0);
  background: rgba(7,17,27,0);
}
.shopcart .details-bg.fade-leave,
.shopcart .details-bg.fade-enter-to {
  -webkit-backdrop-filter: blur(0);
  backdrop-filter: blur(0);
  background: rgba(7,17,27,0.6);
}


