  background: #fff;
}
.goods .menu-wrapper .menu-list .menu-item.current .text:after {
  border: none;
}
.goods .menu-wrapper .menu-list .menu-item .icon {
  display: inline-block;
  margin: auto 0;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.goods .menu-wrapper .menu-list .menu-item .icon.decrease {
  background-image: url("decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.goods .menu-wrapper .menu-list .menu-item .icon.decrease {
    background-image: url("decrease_3@3x.png");
}
}
.goods .menu-wrapper .menu-list .menu-item .icon.discount {
  background-image: url("discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.goods .menu-wrapper .menu-list .menu-item .icon.discount {
    background-image: url("discount_3@3x.png");
}
}
.goods .menu-wrapper .menu-list .menu-item .icon.special {
  background-image: url("special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.goods .menu-wrapper .menu-list .menu-item .icon.special {
    background-image: url("special_3@3x.png");
}
}
.goods .menu-wrapper .menu-list .menu-item .icon.invoice {
  background-image: url("invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.goods .menu-wrapper .menu-list .menu-item .icon.invoice {
    background-image: url("invoice_3@3x.png");
}
}
.goods .menu-wrapper .menu-list .menu-item .icon.guarantee {
  background-image: url("guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.goods .menu-wrapper .menu-list .menu-item .icon.guarantee {
    background-image: url("guarantee_3@3x.png");
}
}
.goods .menu-wrapper .menu-list .menu-item .text {
  display: table-cell;
  vertical-align: middle;
  position: relative;
  font-size: 12px;
  line-height: 14px;
  color: #4d555d;
}
.goods .menu-wrapper .menu-list .menu-item .text:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: "";
}
.goods .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .foods-list .title {
  height: 26px;
  font-size: 12px;
  line-height: 26px;
  padding-left: 14px;
  border-left: 2px solid #d9dde1;
  color: #93999f;
  background: #f3f5f7;
}
.goods .foods-wrapper .foods-list .foods-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 18px;
  padding: 18px 0;
  font-size: 0;
  position: relative;
}
.goods .foods-wrapper .foods-list .foods-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: "";
}
.goods .foods-wrapper .foods-list .foods-item:last-child:after {
  border: 0;
}
.goods .foods-wrapper .foods-list .foods-item .food-icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
}
.goods .foods-wrapper .foods-list .foods-item .food-icon img {
