  height: 57px;
}
.goods .foods-wrapper .foods-list .foods-item .food-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  margin-left: 10px;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .name {
  font-size: 14px;
  line-height: 14px;
  font-weight: 700;
  margin: 2px 0 8px 0;
  color: #07111b;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .description,
.goods .foods-wrapper .foods-list .foods-item .food-content .sell {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .sell {
  line-height: 10px;
  margin-top: 8px;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .sell :first-child {
  margin-right: 12px;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .price {
  line-height: 24px;
  font-weight: 700;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .now-price {
  display: inline-block;
  font-size: 14px;
  color: #f01414;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .old-price {
  display: inline-block;
  font-size: 10px;
  margin-left: 8px;
  text-decoration: line-through;
  color: #93999f;
}
.goods .foods-wrapper .foods-list .foods-item .food-content .shopbtn-wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
}


