
.ratinglist .ratings-type {
  margin: 0 18px;
  padding: 18px 0;
  font-size: 0;
  position: relative;
}
.ratinglist .ratings-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: "";
}
.ratinglist .ratings-type .rating-type {
  display: inline-block;
  width: 60px;
  height: 32px;
  margin-right: 8px;
  border-radius: 1px;
  text-align: center;
  color: #4d555d;
}
.ratinglist .ratings-type .rating-type.all,
.ratinglist .ratings-type .rating-type.positive {
  background: rgba(0,160,220,0.2);
}
.ratinglist .ratings-type .rating-type.all.active,
.ratinglist .ratings-type .rating-type.positive.active {
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
}
.ratinglist .ratings-type .rating-type.negative {
  background: rgba(77,85,93,0.2);
}
.ratinglist .ratings-type .rating-type.negative.active {
  border-radius: 12px;
  color: #fff;
  background: #4d555d;
}
.ratinglist .ratings-type .rating-type .name {
  display: inline-block;
  font-size: 12px;
  line-height: 32px;
}
.ratinglist .ratings-type .rating-type .count {
  margin-left: 2px;
  font-size: 8px;
}
.ratinglist .content-type {
  padding: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  font-size: 0;
  color: #93999f;
}
.ratinglist .content-type .icon-check_circle {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
}
.ratinglist .content-type .icon-check_circle.active {
  color: #00c850;
}
.ratinglist .content-type .text {
  display: inline-block;
  vertical-align: top;
  margin-left: 4px;
  font-size: 12px;
  line-height: 24px;
}


