
.header {
  color: #fff;
  position: relative;
  overflow: hidden;
  background: rgba(7,17,27,0.5);
}
.content-wrapper {
  padding: 24px 12px 18px 24px;
  position: relative;
  font-size: 0;
}
.content-wrapper .avatar {
  display: inline-block;
}
.content-wrapper .avatar img {
  border-radius: 2px;
  display: block;
}
.content-wrapper .content {
  display: inline-block;
  height: 100%;
  vertical-align: top;
  margin-left: 16px;
}
.content-wrapper .content .title {
  margin: 2px 0 8px 0;
  font-size: 0;
}
.content-wrapper .content .title .name {
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  margin-left: 6px;
}
.content-wrapper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
  vertical-align: top;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.content-wrapper .content .description {
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 12px;
}
.content-wrapper .content .supports {
  margin-bottom: 2px;
  font-size: 0;
}
.content-wrapper .content .supports .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.content-wrapper .content .supports .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .supports .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.content-wrapper .content .supports .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .supports .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.content-wrapper .content .supports .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .supports .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.content-wrapper .content .supports .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .supports .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.content-wrapper .content .supports .icon.special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.content-wrapper .content .supports .icon.special {
    background-image: url("special_1@3x.png");
}
}
.content-wrapper .content .supports .text {
  margin-left: 4px;
  font-size: 10px;
  line-height: 12px;
}
.content-wrapper .supports-count {
  position: absolute;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  right: 12px;
  bottom: 15px;
  text-align: center;
  font-size: 0;
  padding: 0 8px;
}
.content-wrapper .supports-count .count {
  font-size: 10px;
  vertical-align: top;
}
.content-wrapper .supports-count .icon-keyboard_arrow_right {
  margin-left: 2px;
  font-size: 10px;
  line-height: 24px;
}
.bulletin-wrapper {
  height: 28px;
  padding: 0 22px 0 12px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  background: rgba(7,17,27,0.2);
}
.bulletin-wrapper .bulletin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  vertical-align: middle;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.bulletin-wrapper .bulletin-text {
  margin: 0 4px;
  font-size: 10px;
  line-height: 28px;
}
.bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  top: 9px;
  right: 12px;
}
.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.background img {
  width: 100%;
  height: 100%;
}
.detail {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  overflow: auto;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.detail.fade-enter-active,
.detail.fade-leave-active {
  transition: all 0.5s;
}
.detail.fade-enter,
.detail.fade-leave-active {
  opacity: 0;
}
.detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.detail .detail-wrapper .detail-main .detail-name {
  text-align: center;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  margin-bottom: 16px;
}
.detail .detail-wrapper .detail-main .star-wrapper {
  text-align: center;
  height: 24px;
}
.detail .detail-wrapper .detail-main .detail-supports {
  width: 80%;
  margin: 0 auto;
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item {
  margin: 0 12px 12px 12px;
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item:last-child {
  margin-bottom: 0;
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  vertical-align: middle;
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.decrease {
  background-image: url("decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.decrease {
    background-image: url("decrease_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.discount {
  background-image: url("discount_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.discount {
    background-image: url("discount_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.special {
  background-image: url("special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.special {
    background-image: url("special_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.invoice {
  background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.invoice {
    background-image: url("invoice_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.guarantee {
  background-image: url("guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-aspect-ratio: 3) {
.detail .detail-wrapper .detail-main .detail-supports .supports-item .icon.guarantee {
    background-image: url("guarantee_2@3x.png");
}
}
.detail .detail-wrapper .detail-main .detail-supports .supports-item .text {
  display: inline-block;
  margin-left: 6px;
  font-size: 12px;
  line-height: 12px;
  vertical-align: middle;
}
.detail .detail-wrapper .detail-main .detail-bulletin {
  width: 80%;
  margin: 0 auto;
}
.detail .detail-wrapper .detail-main .detail-bulletin .bulletin-content {
  padding: 0 12px;
  font-size: 12px;
  line-height: 24px;
}
.detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  clear: both;
  margin: -64px auto 0 auto;
  font-size: 32px;
  color: rgba(255,255,255,0.5);
}


