
.shopbtn {
  font-size: 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.shopbtn .shop-decrease {
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  transition: all 0.3s linear;
}
.shopbtn .shop-decrease.move-enter,
.shopbtn .shop-decrease.move-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0) rotate(180deg);
          transform: translate3d(24px, 0, 0) rotate(180deg);
}
.shopbtn .shop-decrease.move-leave,
.shopbtn .shop-decrease.move-enter-to {
  opactiy: 1;
  -webkit-transform: translate3d(0, 0, 0) rotate(0);
          transform: translate3d(0, 0, 0) rotate(0);
}
.shopbtn .shop-count {
  display: inline-block;
  vertical-align: top;
  width: 18px;
  text-align: center;
  font-size: 10px;
  line-height: 36px;
  color: #93999f;
}
.shopbtn .shop-add {
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}


